.word-header-arrow-text-container {
  display: flex;
  height: 10vh;
  padding: 0;
}
@media (max-width: 1200px) {
  .word-header-arrow-text-container {
    display: flex;
    height: 10vh;
    margin: 0;
  }
}

.header-arrow-container {
  display: flex;
  height: 100%;
  padding-top: 0.7vh;
  margin-right: 5px;
}
.moth-words-arrow {
  height: 1.2vh;
}

.header-text-container {
  color: black;
  font-size: 2vh;
}

//words
.moth-words-class {
  font-weight: 600;
  font-size: 2.5vh;
  //   color: #ecdb00;
  color: black;
}

.moth-words-table {
  width: 90vw;
  text-align: left;
  white-space: nowrap;
  overflow: scroll;
}

.moth-words-button {
  font-size: 2vh;
  padding: 0.1vh 0.7vh;
  border-width: 2px;
}

//https://www.w3schools.com/charsets/ref_utf_dingbats.asp
.moth-words-button::before {
  content: "＋ ";
  font-weight: 600;
  display: inline-block;
  font-size: 2vh;
  width: 3vh;
}

.moth-words-button:not(:disabled):not(.disabled):active,
.moth-words-button:not(:disabled):not(.disabled).active,
.show > .moth-words-button.dropdown-toggle {
  color: #ecdb00;
  background-color: #343a40;
  border-color: #343a40;
}

.moth-words-button:not(:disabled):not(.disabled):active::before,
.moth-words-button:not(:disabled):not(.disabled).active::before,
.show > .moth-words-button.dropdown-toggle::before {
  content: " \2713";
  font-weight: 600;
  display: inline-block;
  font-size: 2vh;
  width: 3vh;
}

