.transparent {
  opacity: 0;
}

.test {
  opacity: 1;
}

.landing-logo {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 10vh;
  transform: translate(-50%, -50%);
}

.landing-enter {
  opacity: 0;
}

.landing-enter-active {
  opacity: 1;
  transition: opacity 2000ms;
}
.landing-enter-done {
  opacity: 1;
}

.landing-exit {
  opacity: 1;
}

.landing-exit-active {
  opacity: 0;
  transition: opacity 2000ms;
}
.landing-exit-done {
  opacity: 0;
}

.landing-text-container {
  position: absolute;
  top: 43vh;
  width: 100vw;
  //   height: 30vh;
  opacity: 0;
}

.landing-text {
  text-align: center;
}

.landing-text-enter {
  opacity: 0;
}
.landing-text-enter-active {
  opacity: 1;
  transition: opacity 2000ms;
}

.landing-text-enter-done {
  opacity: 1;
}

