.event-header-arrow-text-container {
  display: flex;
  height: 10vh;
  padding: 0;
}

.header-text-event-special {
  display: inline-block;
  color: white;
  background-color: black;
  margin-bottom: 0.3vh;
}

.moth-event-arrow {
  height: 1.2vh;
}

// Grids
.grid-5-cols {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}

.upper-event-grid {
  display: grid;
  grid-template-columns: repeat(35, 1fr);
  grid-template-rows: repeat(2, 1fr);
}

.lower-event-grid {
  display: grid;
  grid-template-columns: repeat(35, 1fr);
  grid-template-rows: repeat(3, 1fr);
}
// for marquee, we can't use this:
// .upper-event-grid div {
//   overflow: hidden;
// }
// .lower-event-grid div {
//   overflow: hidden;
// }

// grid-row-start / grid-column-start / grid-row-end / grid-column-end.
.ticker1 {
  grid-area: 1 / 1 / span 1 / span 16;
}
.ticker2 {
  grid-area: 2 / 17 / span 1 / span 19;
}

.event-swiper {
  grid-area: 3 / 1 / span 3 / span 35;
}

.ticker3 {
  grid-area: 1 / 1 / span 1 / span 25;
}

.ticker4 {
  grid-area: 2 / 26 / span 1 / span 10;
}

.ticker5 {
  grid-area: 3 / 6 / span 1 / span 20;
}

// Ticker
.ticker-content {
  color: black;
  font-size: 2vh;
  white-space: nowrap;
}

.ticker-title {
  background-color: #ecdb04;
  font-weight: 600;
  font-size: 5.5vh;
  white-space: nowrap;
  display: flex;
  align-items: center;
  justify-content: center;
}

// Swiper

// .swiper-container {
//   width: 100%;
//   padding-top: 20px;
//   padding-bottom: 50px;
// }

.swiper-container {
  height: 100%;
  background-color: white;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
}

.swiper-event-image {
  height: 90%;
  margin-left: auto;
  margin-right: auto;
}

.swiper-container-horizontal > .swiper-pagination-bullets {
  bottom: 3%;
}
.swiper-pagination-bullet {
  background-color: #131101; //pagination color
}

.swiper-button-next {
  --swiper-theme-color: #131101;
  transform: translateX(-2vw)
    translateY(calc(-0.35 * var(--swiper-navigation-size)));
  margin-top: 0 !important;
}
.swiper-button-prev {
  --swiper-theme-color: #131101;
  transform: translateX(2vw)
    translateY(calc(-0.35 * var(--swiper-navigation-size)));
  margin-top: 0 !important;
}
// .swiper-slide-cards {
//   width: 100vh;
//   height: 20vh;
// }

// marquee
// override the default css, but don't know why
.marquee-container {
  height: 25% !important;
}
@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-100%));
  }
}

.marquee {
  white-space: nowrap;
  margin-right: 0px;
}

.marquee-content {
  color: black;
  font-size: 1.9vh;
  white-space: nowrap;
}
