.result-image-container {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: flex-end;
}

// ===
//result image and the animation
// ===

.result-image {
  height: 60%;
  margin-left: auto;
  margin-right: auto;
  // adjust here to make an animation
  position: relative;
}
.result-image-enter {
  opacity: 0;
  bottom: 0%;
}
.result-image-enter-active {
  opacity: 1;
  bottom: 40%;
  transition: bottom 2000ms;
}

.result-image-enter-done {
  opacity: 1;
  bottom: 40%;
}

