// ===
//result text and the corresponding animation
// ===
.result-text-container {
  height: 30vh;
}

.result-text {
  text-align: center;
}

.result-text-enter {
  opacity: 1;
}
.result-text-enter-active {
  opacity: 0;
  transition: opacity 2000ms;
}

.result-text-enter-done {
  opacity: 0;
  height: 0;
}

