  opacity: 0;
  margin-top: -13vh;
}

.result-icon-section {
  margin-top: 5vh;
}

.result-icon-container {
  display: flex;
  justify-content: center;
}

.result-icon {
  height: 5vh;
  margin: 35px;
  margin-top: 0px;
}

a:hover {
  cursor: pointer;
}

.result-info-enter {
  opacity: 0;
}
.result-info-enter-active {
  opacity: 1;
  transition: opacity 2000ms;
}

.result-info-enter-done {
  opacity: 1;
}

