// ===
//result overlay color
// ===
.result-overlay-color {
  background-color: #eddb00;
  opacity: 0.3;
  height: 1000vh;
  width: 1000vw;
  position: absolute;
  top: 0;
  left: 0;
}

.result-overlay-color-enter {
  opacity: 0.3;
}
.result-overlay-color-enter-active {
  opacity: 0;
  transition: opacity 2000ms;
}

.result-overlay-color-enter-done {
  opacity: 0;
  height: 0;
}

// double arrow and title
.moth-result-chosen-title {
