// for overall colors, see setting/base/_colors.scss

body {
  height: 90vh;
  background-color: white;
}

body,
button,
input,
select,
textarea {
  font-family: "Noto San TC", serif;
  font-weight: 600;
}

.btn {
  font-family: "Noto San TC", serif;
  font-weight: 600;
}

tr {
  border-bottom: none;
}
td {
  padding: 0.3vh;
}

.arrow-in-button {
  color: #101820;
}

.moth-bottom {
  width: 100%;
  text-align: center;
  position: absolute;
  bottom: 0px;
}

.bottom-15 {
  bottom: 15px;
}

.scroll-block {
  width: 100vw;
  overflow: scroll;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
}

.moth-text {
  color: black;
  text-align: left;
  font-size: 2vh;
}

.ta-l {
  text-align: left;
}

::-webkit-scrollbar {
  display: none;
}
